<template>
  <Titulo texto="Ruta con parámetros para Blog" />
  <h2>Parámetro: {{ $route.params.id }}</h2>
  <h3>Title: {{ articulo.title }}</h3>
  <p>id: {{ articulo.id }} - Content: {{ articulo.body }}</p>
</template>

<script>
import Titulo from '../components/Titulo';

export default {
    components: {
        Titulo
    },
    data(){
        return {
            articulo: {}
        }
    },
    methods: {
        async consumurArticulo(){
            try {
                const data = await fetch(`https://jsonplaceholder.typicode.com/posts/${this.$route.params.id}`)
                const objeto = await data.json()
                console.log(objeto)
                this.articulo = objeto
            } catch (error) {
                console.log(error)
            }
        }
    },
    created(){
        this.consumurArticulo()
    }
}
</script>

